




















import { getUrlParam } from '@/utils/common.ts'
import { Component, Vue } from 'vue-property-decorator';
@Component({
  components: {},
})
export default class ViewComponent extends Vue {
  /**data */
  selectedLabel = '' // 充值页，成功页，失败页
  model = {
    amount: '',
  }
  schema = {
    fields: [
      {
        type: "input",
        modelKey: "amount",
        label: "￥",
        props: {
          placeholder: "输入金额",
          clearable: true,
        },
        rules: {
          required: true,
        },
        trigger: "blur",// *待优化：这样会触发多次异步请求校验。思路一：仅当提交表单时才发起一次异步校验
        messages: {
          required: "不能为空",
        },
      },
      {
        type: "submit",
        label: "确认"
      }
    ]
  }

  /**life hook */
  created(){
    if(getUrlParam('result') === 'true'){
      this.selectedLabel = '成功页'
    }
    else if(getUrlParam('result') === 'false'){
      this.selectedLabel = '失败页'
    }
    else{
      this.selectedLabel = '充值页'
    }
  }
  mounted(){
    window.scrollTo(0, 0) // 兼容微信输入框，虚拟键盘消失后不回弹问题
  }

  /**method */
  async submitHandler(e: any) {
    e.preventDefault();
    const data = await this.$request({
      api: 'dkdPersonChargeConfirm', // 2.充值确认  --懒猫充值相关
      params: {
        amount: this.model.amount,
        token:this.$store.state.token,
        originReturnUrl:this.$store.state.originReturnUrl,
      },
      method:'post',
    })
    if(data.resCode === '0000'){
      this.$request({
        formSubmitData:{
          url:data.shUrl,
          params:data.params,
          returnUrl:data.returnUrl,
        }
      })
    }
  }
}
